  .contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 400px;
    padding: 20px 0;
    margin: 0 auto;
    .custom__headline{
      position: relative;
      margin-bottom: 70px;
      text-align: center;
      font-size: 42px;
      font-weight: 400;
      z-index: 100;
    }
    .custom__headline:after{
      .headline-after--center();

    }
    .custom__text{
      text-align: center;
      color: rgba(1,1,1, 0.6);
      margin-bottom: 40px;
    }
    .contact-form-wrapper{
      display: flex;
      flex-wrap: wrap;
      width: 100%;

      padding: 0 20px;

      .contact-form__item{
        width: 100%;
        padding: 10px 10px 10px 5px;
        border: none;
        border-bottom: 1px solid rgba(1,1,1, 0.2);
        // margin: 0 20px 20px 20px;
        margin-bottom: 10px;
        outline: none;

      }
      .contact-form__item--width{
        width: 100%;
        min-height: 100px;
      }
      .contact__btn{
        padding: 15px 40px;
        margin: 10px auto 0 auto;
        background-color: #ff8b38;
        color: #fff;
        font-weight: 600;
        border-radius: 5px;

        transition: 0.3s;

        &:hover{
          background-color: #333;
        }
      }
    }

  }

@media screen and(min-width: @tb) {
