  .custom{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 0;
    padding: 30px 20px;
    background-image: url("../image/state.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: #fff;

    .custom__headline{
      position: relative;

      margin-bottom: 70px;
      font-size: 32px;
      font-weight: 400;
      z-index: 100;
      text-align: center;
    }
    .custom__headline:after{
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: -35px;
      margin: auto;
      width: 70px;
      height: 2px;
      background-color: #fff;
    }
    .custom__text{
      position: relative;

      z-index: 100;
      color: rgba(255,255,255, 0.5);
      margin-bottom: 30px;

      text-align: center;
    }
    .custom__btn{
      display: block;
      position: relative;
      width: 280px;
      z-index: 100;
      color: #fff;
      padding: 15px 0;
      border: 2px solid #fff;
      font-weight: 600;
      transition: 0.3s;
      margin: 0;
      &:hover{
        background-color: #ff8b38;
        border-color: transparent;
      }
    }
  }
  .custom:after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(1,1,1, 0.5);
    z-index: 0;
  }
