.header{
  background-color: #000000;
  border-bottom: 2px solid #ffffff;
  padding: 20px 20px 10px 20px;
  transition: 0.3s;
  .header-wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
    .logo{
      font-size: 32px;
      color: #ffffff;
      font-weight: 800;
      margin-bottom: 5px;
      order: 1;

      span{
        padding-left: 3px;
        color: #884a1e;
        font-weight: 300;
      }
    }
    .nav{
      display: flex;
      justify-content: space-around;
      width: 100%;
      order: 3;
      padding-top: 20px;


      .nav__item{
        font-size: 12px;
        margin-right: 10px;
        color: white;
        font-weight: 600;
      }
      .nav__item:last-child{
        margin-right: 0;
      }
      .nav__item:hover{
        color: #884a1e;
      }
    }

    .burger-menu {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 40px;
      height: 40px;
      cursor: pointer;
      order: 2;
      .line {
        position: relative;
        width: 100%;
        height: 2px;
        margin-bottom: 5px;
        background-color: white;
        transition: 0.3s;
        cursor: pointer;
      }
      .line-one-active {
        transform: rotate(45deg);
        margin: 0;
        top: 2px;
      }
      .line-two-active {
        transform: rotate(-45deg);
        margin: 0;
      }
    }
    .hide{
      display: none;

    }
  }

}
  @media screen and(min-width: @mb) {
    .header{
      padding-bottom: 10px;
      .header-wrapper{
        .logo{
        }
        .nav{
          display: flex;
          width: auto;
          padding: 0;
          .nav__item {
            font-size: 16px;
            margin-right: 20px;
          }
        }
        .burger-menu {
          display: none;
        }
      }
    }
  }
  @media screen and(min-width: @tb) {
    .header{
      padding: 20px 40px 10px 40px;
      .header-wrapper{

        .logo{

        }
        .nav{

          .nav__item{
            margin-right: 30px;

          }
        }
      }
    }
  }
  @media screen and(min-width: @ds) {
    .header{
      padding: 20px 80px 20px 80px;
      .header-wrapper{

        .logo{

        }
        .nav{
          .nav__item{
            font-size: 18px;
            margin-right: 45px;
            transition: 0.2s;
          }
          .nav__item:hover{
            transform: scale(1.3);
          }
        }
      }
    }
  }

