  .process{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    .process__headline{
      position: relative;
      width: 90%;
      font-size: 38px;
      text-align: center;
      margin: 0 auto 60px auto;

      font-weight: 400;
      padding: 20px 0 0 0;
    }
    .process__headline:after{
      .headline-after--center();
    }
    .process__text{
      width: 90%;
      text-align: center;
      font-size: 16px;
      padding: 0;
      margin: 0 auto 40px auto;
      color: rgba(1,1,1,0.6);
    }
    .process-item{
      display: flex;
      flex-direction: column;
      align-items:center;
      width: 100%;
      margin-bottom: 20px;
      padding: 0 20px;
      .process-item__image{
        margin-bottom: 15px;
      }
      .process-item__headline{
        margin-bottom: 15px;

      }
      .process-item__text{
        text-align: justify;
        color: rgba(1,1,1,0.6);
      }
    }
  }
@media screen and(min-width: @tb) {
  .process{
    flex-direction: row;
    padding: 0 0 20px 0;
    .process__headline{
      font-size: 42px;
    }
    .process__headline:after{
      .headline-after--center();
    }
    .process__text{

    }
    .process-item{
      width: calc(100% / 3);
      .process-item__image{

      }
      .process-item__headline{
        font-size: 18px;

      }
      .process-item__text{

      }
    }
  }
}
