  .tabs{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    font-family: 'Open Sans', sans-serif;
    padding: 20px 20px;
    background-color: #f5f5f5;
    .tabs-btn{
      display: flex;
      justify-content: center;
      margin-bottom: 20px;
      width: 100%;
      .tabs-btn__item{
        display: flex;
        justify-content: center;
        align-items:center;
        width: 100px;
        height: 50px;
        color: #fff;
        font-size: 16px;
        font-weight: 700;
        background-color: #333333;
        border-right: 2px solid #c7c7c7;
        cursor: pointer;
      }
      .tabs-btn__item:hover{
        background-color: #ff8b38;
      }

    }
    .tabs-content{
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 5px;
      transition: 0.5s;
      .tabs-content__headline{
        margin-bottom: 20px;
        font-size: 18px;
      }
      .tabs-content__text{
        width: 100%;
        font-size: 14px;
        margin-bottom: 30px;
        color: rgba(1,1,1, 0.6);
        text-align: justify;
      }
      .tabs-content__btn{
        padding: 20px 40px;
        margin-bottom: 40px;
        color: #fff;
        border-radius: 5px;
        font-weight: 700;
        background-color: #ff8b38;
        cursor: pointer;
        transition: 0.3s;
        &:hover{
          background-color: #ff8b38;
          background-color: #333;
        }
      }
    }
    .tabs__img{
      width: 100%;
      height: 150px;

    }
    .tabs__img--width{
      width: 100%;
      height: 150px;
    }
    .tabs__img--margin{
      margin-top: 50px;
    }
    .show{
      display: flex;
      animation: show 1s;
    }
    .hide{
      display: none;
    }
    @keyframes show {
      from {
        opacity: 0;
      }

      to {
        opacity: 1;
      }
    }
  }
