.section {
  display: flex;
  flex-direction: column;
  .section__img {
    width: 100%;
    height: auto;
    order: 2;
  }
  .wrapper {
    order: 1;
    .section__headline {
      padding: 20px;
      .headline();
    }
    .section__headline:after {
      .headline-after--left();
      left: 20px;
    }
    .section__text {
      display: flex;
      flex-wrap: wrap;
      text-align: justify;
      padding: 0 20px;
      margin-bottom: 20px;
      span {
        position: relative;
        width: 100%;
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 10px;

      }

      .text-icon-one:after {
        content: "";
        position: absolute;
        top: -15px;
        left: 50px;
        width: 40px;
        height: 40px;
        background-image: url("../image/icon.png");
        cursor: pointer;
      }
      .text-icon-two:after {
        content: "";
        position: absolute;
        top: -15px;
        left: 50px;
        width: 40px;
        height: 40px;
        background-image: url("../image/icon2.png");
             cursor: pointer;
      }
    }

  }
}
.section--bg {
    background-color: #f5f5f5;

}
@media screen and(min-width: @mb) {
  .section {

    .section__img {

    }
    .wrapper {
      .section__headline {

      }
      .section__text {
        padding: 0 20px;
      }
    }
  }
}
@media screen and(min-width: @tb) {
  .section {
    flex-direction: row;
    .section__img {
      width: 50%;
      height: 300px;
      align-self: center;
    }
    .img-order {
      order: 1;
    }
    .wrapper {
      width: 50%;
      .section__headline {
        padding: 20px;
      }
      .section__text {
        padding: 0 20px;
      }
    }
    .wrapper-order {
      order: 2;
    }
  }
}
@media screen and(min-width: @ds) {
  .section {
    flex-direction: row;
    .section__img {
      width: 50%;
      height: 400px;
      align-self: center;
    }
    .img-order {
      order: 1;
    }
    .wrapper {
      width: 50%;
      .section__headline {
        padding: 20px;
      }
      .section__text {
        padding: 0 20px;
      }
    }
    .wrapper-order {
      order: 2;
    }
  }
}
