.footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #000;
  padding: 20px;

  .wrapper-social{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    width: 100%;
    padding: 0;
    .social__item{
      width: 25px;
      height: 25px;
      margin-right: 50px;
      fill: #fff;
      cursor: pointer;
      padding: 0;
      margin: 0;
    }
  }
  .footer__bottom-text{
    color: #fff;
  }
}

@media screen and (min-width: @ds) {
.footer{


  .wrapper-social{
    justify-content: space-around;
    .social__item{

    }
  }
  .footer__bottom-text{
  }
}

}
