  .testimonials{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 20px 0;
    .testimonials__headline{
      position: relative;
      width: 100%;
      font-size: 32px;
      text-align: center;
      margin-bottom: 60px;
      font-weight: 400;

    }
    .testimonials__headline:after{
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: -35px;
      margin: auto;
      width: 70px;
      height: 2px;
      background-color: #ff8b38;
    }
    .testimonials__text{
      width: 100%;
      text-align: center;
      font-size: 16px;
      margin-bottom: 70px;
      color: rgba(1,1,1,0.6);
    }
    .testimonials-item{
      display: flex;
      flex-wrap: wrap;
      width: 80%;
      margin: 0 auto 20px auto;
      .testimonials-item__text{
        position: relative;
        width: 100%;
        padding: 20px 10px;
        background-color: #f5f5f5;
        margin-bottom: 20px;
        order: 3;
      }

      .testimonials-item__image{
        width: 50px;
        height: 50px;
        background-color: #f2f2f2;
        margin-right: 20px;
        border-radius: 50%;
        margin: 0 20px 20px 20px;
        order:1;
      }
      .testimonials-item__name{
        display: flex;

        flex-wrap: wrap;
        font-weight: 600;
        order:2;
        span{
          font-size: 14px;
          font-weight: 100;
          width: 100%;
        }
      }
    }
  }
