  .social{
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 2px 2px black;
    margin-bottom: 3px;
    padding: 10px 0;
    .social-wrapper{
      width: 100%;
      padding: 0 20px;
      margin-bottom: 20px;
      .social__headline{
        font-weight: 700;
        font-size: 26px;
        font-family: 'Open Sans', sans-serif;
        color: rgba(1,1,1,0.9);
        margin-bottom: 10px;
        padding-top: 20px;
      }
      .social__text{
        color: rgba(1,1,1,0.6);
      }
    }
    .wrapper-social{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding: 0 20px 10px 20px;
      .social__item{
        width: 25px;
        height: 25px;
        fill: rgba(1,1,1, 0.5);
        cursor: pointer;
      }
      .social__item:last-child{
        margin-right: 0;
      }
    }
  }
