.banner{
  position: relative;
  display: flex;
  flex-direction: column;
  background-image: url("../image/bg-Banner.png");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Raleway', sans-serif;
  .banner-wrapper{
    display: flex;
    flex-direction: column;
    color: #ffffff;
    padding: 50px 20px 40px 20px;
    z-index: 100;
    .logo--margin-font-size{
      font-size: 32px;
      margin-bottom: 10px;
      span{
        color: #884a1e;
      }
    }
    .banner__tagline{
      position: relative;
      display: flex;
      flex-direction: column;
      font-size:16px;
      font-weight: 400;
      margin-bottom: 70px;
      color: rgba(255,255,255, 0.9);
    }
    .banner__tagline:after{
      .headline-after--left();
      background-color: white;
    }
    .banner__text{
      width: 100%;
      color: rgba(255,255,255, 0.7);
      margin-bottom: 45px;
    }
    .banner__btn{
      padding: 10px 30px;
      border: 2px solid #fff;
      color: #fff;
      align-self: baseline;
      cursor: pointer;
      font-weight: 600;
      transition: 0.3s;

    }
    .banner__btn:hover{
      background-color: #884a1e;
      border-color: transparent;
    }
  }
  .banner-form{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;

    color: #fff;
    background-color: #fff;
    z-index: 100;
    .banner-form__headline{
      font-size: 18px;
      font-weight: 600;
      text-align: center;
      color:#000;
      background-color: #f5f5f5;
      padding: 15px 0;
      margin-bottom: 40px;
      span{
        color: #ff8b38;
      }
    }
    .banner-form__item{
      width: 90%;
      margin: 0 auto 15px auto;
      padding: 10px 10px;
      font-weight: 600;
      border: none;
      border-bottom: 1px solid rgba(1,1,1, 0.3);
      background-color: transparent;
      color: rgba(1,1,1, 1);
      outline: none;
    }
    .banner-form__btn{
      padding: 15px 0;
      font-weight: 600;
      background-color: @site-color;
      color: #fff;
      transition: 0.3s;
      &:hover{
        background-color: @btn-hover-color;
      }
    }
  }
}
@media screen and(min-width: @mb) {
  .banner {
    flex-direction: row;
    align-items: flex-start;
    padding-bottom: 40px;
    .banner-wrapper{
      width: 50%;
      .banner__text {
        width: 100%;
      }
    }
    .banner-form {
      width: calc(50% - 20px);
      max-width: 300px;
      margin-top: 50px;
      .banner-form__headline{
        margin-bottom: 20px;
      }
      .banner-form__item {
        margin-bottom: 20px;
      }
    }
  }
}

