  .pricing{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0;
    background-color: #f5f5f5;

    .pricing__headline{
      position: relative;
      width: 100%;
      font-size: 32px;
      text-align: center;
      padding: 20px 0 0 0;
      margin-bottom: 60px;
      font-weight: 400;
    }
    .pricing__headline:after{
      .headline-after--center();
    }
    .pricing__text{
      width: 100%;
      text-align: center;
      font-size: 16px;
      margin-bottom: 40px;
      color: rgba(1,1,1,0.6);
    }
    .pricing-item{
      width: 100%;
      display: flex;
      flex-direction: column;
      text-align: center;
      background-color: #fff;
      transition: 0.5s;
      border-bottom: 10px solid green;
      .pricing-item__headline{
        padding: 15px 0;
        color: #fff;
        background-color: #333;
        border-bottom: 1px solid #fff;
      }
      .pricing-item__tagline{
        color: #fff;
        background-color: #333;
        span{
          font-size: 72px;
          font-weight: 600;

        }
      }
      .pricing-item__text{
        padding: 15px 0;
        border-bottom: 1px solid rgba(1,1,1, 0.2);
      }
      .pricing-item__text:last-child{
        border:none;
      }
      .arr{
        position: relative;

        &::after{
          content: "OUR MOST POPULAR";
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          left: 0;
          top: -15px;
          height: 15px;
          background-color: #ff8b38;
          color: #fff;
          overflow: hidden;
          transition: 0.3s;
          font-size: 0;
          font-weight: 600;
        }
      }
      .pricing-item__text--style{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: -30px;
        color: #fff;
        background-color: #ff8b38;
        font-weight: 600;
        overflow: hidden;
        padding: 0;
        height: 40px;
        border-bottom: 5px solid #ff8b38;
        transition: 0.5s;
        display: none;
      }
      .pricing-item__text--tagline{
        background-color: #333;
        color: rgba(255,255,255, 0.5);
        transition: 0.3s;
        padding-bottom: 45px;
      }
    }
    .pricing-item:last-child {
      // border: none;
    }
    .pricing-item:hover .arr::after{
      top: -40px;
      height: 40px;
      font-size: 16px;
    }
    .pricing-item:hover .pricing-item__text--tagline{


    }

    .pricing-item:hover .pricing-item__text--style{
/*    padding: 15px;
      height: 40px;
*/

    }
    .pricing-item:hover{
     //transform: translateY(-40px);

    }
  }
